import StoreStatus from "@/constants/StoreStatus";
import api from "~/services/api";

export default {
    layout: "main-content",
    data() {
        return {
            StoreStatus,
            stores: null
        }
    },
    created() {
        api.get('store', this.$cookies.get('token'))
            .then(res => {
                this.stores = res.data.data.data
            })
    },
};
